import React,{useContext,useState,useEffect} from 'react';
import { MyContext } from '../../contexts/MyContext';
import { WEB_URL_UPLOAD } from '../../contexts/Apilinks';
let BaseURLuploads = WEB_URL_UPLOAD;
let uploadimg = BaseURLuploads + 'noimage.jpg';


const Propertyviewimage = (props) => {
  const {allimgsss} = useContext(MyContext);
      useEffect(() => {
       proimgsss(props.ids);
  }, []);
  const [isproimgsss, setproimgsss] = useState([]);
  const [isproimgsssone, setproimgsssone] = useState([]);
  const [isdefault, setproimgdefault] = useState([]);
  const proimgsss = async(ids) =>{
    try {
      const proimgsssqqq = await allimgsss(ids);
      if(proimgsssqqq.success === 1){
        console.log('proimgsssqqq',proimgsssqqq.imgviews);
        setproimgsss(proimgsssqqq.imgviews);
        setproimgdefault(proimgsssqqq.imgset);
        if(proimgsssqqq.imgviews.length > 0){ 
        setproimgsssone(proimgsssqqq.imgviews[0].property_image);
        }
      }
    } catch (error) { throw error;}
  }








  // console.log('zzzzzzzzzzzzzzz',isimgorder.map((imageall) => (BaseURLuploads +imageall.property_image
//   )));

//IMAGE ARRAY
//const images = [image1, image2, image3, image4, image5, image6];

const images = isproimgsss.map((imageall) => (imageall.property_image
  ));
  
  const [imageToShow, setImageToShow] = useState("");
  const [lightboxDisplay, setLightBoxDisplay] = useState(false);
  
  //looping through our images array to create img elements
  const imageCards = isproimgsss.map((image) => (
    <img className="image-card" onClick={() => showImage(image.property_image)} src={BaseURLuploads + image.property_image} />
  ));
  
  //function to show a specific image in the lightbox, amd make lightbox visible
  const showImage = (image) => {
    setImageToShow(image);
    setLightBoxDisplay(true);
  };
  
  //hide lightbox
  const hideLightBox = () => {
    setLightBoxDisplay(false);
  };
  
  //show next image in lightbox
  const showNext = (e) => {
    e.stopPropagation();
    let currentIndex = images.indexOf(imageToShow);
    if (currentIndex >= images.length - 1) {
      let loopsimg = isproimgsss.length - 1;
      let nextImage = images[currentIndex - loopsimg ];
      setImageToShow(nextImage);
      //setLightBoxDisplay(false);
    } else {
      let nextImage = images[currentIndex + 1];
      setImageToShow(nextImage);
    }
  };
  
  //show previous image in lightbox
  const showPrev = (e) => {
    e.stopPropagation();
    let currentIndex = images.indexOf(imageToShow);
    if (currentIndex <= 0) {
      let loopsimg = isproimgsss.length - 1;
      let nextImage = images[currentIndex + loopsimg ];
      setImageToShow(nextImage);
     // setLightBoxDisplay(false);
    } else {
      let nextImage = images[currentIndex - 1];
      setImageToShow(nextImage);
    }
  };

  


  return (
    <>
      


{isproimgsss.length > 0 ? <> 

  {isdefault.length > 0 ? <> 
    {isdefault.map((itemsql, index) => (  
      <div className="img__1" key={itemsql.propertyimg_id} >
        <img src={BaseURLuploads + itemsql.property_image} alt={itemsql.phototype} onClick={() => showImage(itemsql.property_image)}/>
        </div>
    ))} 
    </> :
    <img src={BaseURLuploads + isproimgsssone} alt="item.phototype" onClick={() => showImage(isproimgsssone)}/>
  }


  {/* {isproimgsss.map((item, index) => (  
    <div className="img__1" key={item.propertyimg_id} >
       {item.property_image !== '' ? 
          <img src={BaseURLuploads + item.property_image} alt={item.phototype} />
        :  
          <img src={uploadimg} alt="" />
        } 
      </div>
  ))} */}


 </> :
  <img src={uploadimg} alt="" />
 }




{/* <div>{imageCards}</div> */}
{
        lightboxDisplay ? 
        <div id="lightbox" onClick={hideLightBox}>
          <button onClick={showPrev}>⭠</button>
          <img id="lightbox-img" src={BaseURLuploads + imageToShow}></img>
          <button onClick={showNext}>⭢</button>
        </div>
       : ""
      }





</>

  )
}

export default Propertyviewimage
