import React,{useContext,useState,useEffect,Suspense, lazy} from 'react';
import { MyContext } from '../contexts/MyContext';
import {Link } from "react-router-dom"; 
import Propertyviewimage from "./property/Propertyviewimage";
import ReactReadMoreReadLess from "react-read-more-read-less";
import { WEB_URL_UPLOAD } from '../contexts/Apilinks';
let BaseURLuploads = WEB_URL_UPLOAD;
let breadcroumbimg = BaseURLuploads + 'bread2.jpg';

const Homelist = () => {
  const {homebuyall,homerentall} = useContext(MyContext);
  useEffect(() => {
    homebuy();
    homerent();
    window.scrollTo(0, 0);
  }, []);

  const [ishomebuy, sethomebuy] = useState([]);
  const homebuy = async(ids) =>{
    try {
      const homebuysss = await homebuyall(ids);
      if(homebuysss.success === 1){
        console.log('homebuysss',homebuysss.homebuylist);
        sethomebuy(homebuysss.homebuylist);
        return;
      }
    } catch (error) { throw error;}
  }
   const [ishomerent, sethomerent] = useState([]);
  const homerent = async(ids) =>{
    try {
      const homerentsss = await homerentall(ids);
      if(homerentsss.success === 1){
        console.log('homerentsss',homerentsss.homerentlist);
        sethomerent(homerentsss.homerentlist);
        return;
      }
    } catch (error) { throw error;}
  }
  return (
    <>
<div className="sec1"> 
  <div className="container"> 
    <div className="rows">     
    <h1> Buy </h1>
{ishomebuy.map((item, index) => (  
  <div className="col-md-4 " key={item.property_id} >
    <div  className="buy__frontsss2 mr-10"  >
       <Propertyviewimage ids={item.property_id} />
    </div>
    <div className="buy__frontsss">
    <div className="clearfix"></div>
    <h2> <b> {item.Bedrooms !== '' ? <> {item.Bedrooms} BHK </> : null } {item.propertytype} </b> <span> for Sale in {item.Locality}  in {item.City} </span></h2>
      
      <div className="clearfix"></div>
    {item.description !== '' ? <p>
       <ReactReadMoreReadLess
        charLimit={77}
        readMoreText={""}
        readLessText={"read less"}>
         {item.description}
        </ReactReadMoreReadLess>
      </p>: null}

      <Link className="btn btn-primary text-center" to ={`PropertyView/${item.property_id}`}> View </Link>

  </div>
  </div>
))}
</div>
</div>
</div>
<div className="sec2"> 
  <div className="container"> 
    <div className="rows"> 
    <h1> Rent </h1>
{ishomerent.map((item, index) => (  
   <div className="col-md-4 " key={item.property_id} >
   <div  className="buy__frontsss2 mr-10"  >
      <Propertyviewimage ids={item.property_id} />
   </div>
   <div className="buy__frontsss">
   <div className="clearfix"></div>
   <h2> <b> {item.Bedrooms !== '' ? <> {item.Bedrooms} BHK </> : null } {item.propertytype} </b> <span> for Sale in {item.Locality}  in {item.City} </span></h2>
     
     <div className="clearfix"></div>
   {item.description !== '' ? <p>
      <ReactReadMoreReadLess
       charLimit={77}
       readMoreText={""}
       readLessText={"read less"}>
        {item.description}
       </ReactReadMoreReadLess>
     </p>: null}

     <Link className="btn btn-primary text-center" to ={`PropertyView/${item.property_id}`}> View </Link>

 </div>
 </div>
))}
  </div>      
  </div>      
  </div>      
    </>
  )
}

export default Homelist
