import React,{useContext,useState,useEffect,Suspense, lazy} from 'react';
import { MyContext } from '../contexts/MyContext';
import { WEB_URL_UPLOAD } from '../contexts/Apilinks';
import {Link } from "react-router-dom"; 

import Homelist from './Homelist';

const Navbarmenu = lazy(() => import('./menu/Navbarmenu'));
const Footer = lazy(() => import('./Footer'));
const SearchHome = lazy(() => import('./search/SearchHome'));
let BaseURLuploads = WEB_URL_UPLOAD;
let breadcroumbimg = BaseURLuploads + 'banner.jpg';


function Home() {
  
  return (
   <>
   <div className="homesss" style={{backgroundImage:"url(" + breadcroumbimg + ")"}}>
   <Suspense fallback={<div>Loading...</div>}>
      <Navbarmenu />
   </Suspense>
  
   <div className="clearfix"></div>
      <div className="container">
         <div className="row">
            <h1> Find your perfect <span> property </span> for your home </h1>
            <h6> We have over million properties for you</h6>
            <Suspense fallback={<div>Loading...</div>}>
               <SearchHome />
            </Suspense>
         </div>
      </div>
   </div>
   

   <Homelist />
   <div className="clearfix"></div>

   <Suspense fallback={<div>Loading...</div>}>
          <Footer />
      </Suspense>
   </>
  );
}

export default Home;